  import {mapActions} from 'vuex';



  export default {
    name: 'Record',
    props:['editId'],
    data () {
     return {
        test:'',
        item_in:'',
        formData :
          {
            in : '',
            out : '',
            businessId : this.editId,
            created:Date.now()
          },
    }
  },

  methods:{
    ...mapActions('LedgerStore',['firebaseAddRecord']),
    onSubmit(){
      this.firebaseAddRecord(this.formData);
      this.$emit('closeModal',1)

    },
    closeModal(){
      this.$emit('closeModal',1)
    }

  },
  computed:{

  }

}
